<template>
  <div class="login-page">
    <div class="bubble"></div>
    <div class="login">
      <div class="login-box">
        <div class="head">
          <img :src="logo" alt="logo" />
        </div>
        <div class="form">
          <img :src="avatar" class="profile-avatar" />
          <div class="login_from_content">
            <el-form label-width="auto" style="max-width: 600px">
              <div class="form-group">
                <el-input v-model="username" placeholder="请输入登录账号">
                  <template #prefix>
                    <el-icon class="el-input__icon"><UserFilled /></el-icon>
                  </template>
                </el-input>
              </div>
              <div class="form-group">
                <el-input v-model="password" type="password" placeholder="请输入登录密码" show-password>
                  <template #prefix>
                    <el-icon class="el-input__icon"><Lock /></el-icon>
                  </template>
                </el-input>
              </div>
              <div v-if="showGoogleAuth" class="form-group">
                <el-input v-model="googleAuthCode" placeholder="请输入谷歌验证码">
                  <template #prefix>
                    <el-icon class="el-input__icon"><Lock /></el-icon>
                  </template>
                </el-input>
              </div>
              <el-radio-group v-model="rememberMe">
                <el-radio value="1">保持会话</el-radio>
              </el-radio-group>
            </el-form>
            <el-button type="primary" round size="large" class="submit-button" @click="loginData"  :disabled="!username || !password">登录账号</el-button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script lang="ts" setup>

import {ref} from 'vue'
import {ElMessage} from 'element-plus'
import {login} from "@/api/login.ts"
import {UserFilled,Lock} from '@element-plus/icons-vue'
const username = ref('')
const password = ref('')
const rememberMe = ref(false)
const googleAuthCode = ref('')  // 新增字段，保存谷歌验证码
const showGoogleAuth = ref(true)  // 判断是否需要显示谷歌验证码输入框
import logo from '@/assets/login-header.png'
import avatar from '@/assets/avatar.png'

const loginData = async () => {
  if (!username.value || !password.value) {
    ElMessage.error('请输入账号和密码')
    return
  }

    const response = await login({
      username: username.value,
      password: password.value,
      google_code: showGoogleAuth.value ? googleAuthCode.value : '',
    })

    if (response.status) {
      ElMessage.success('登录成功')
      setTimeout(() => {
        window.location.href = '/dashboard'
      }, 1000)

    }

}
</script>
